@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
.light {
	--main-bg-color: #54e487;
	--main-tx-color: #191414;
	--header-tx-color: #edfcf2;
	--tx-color-disable: #3a2e2e;
	--lighter-bg-color: #87ecab;
	--nav-bg-color: #191414;
	--nav-tx-color: #54e487;
	/*divider*/
	--playlist-track: #000;
	--track-hover-color: #f1f1f1;
	--editing-left-bg: #fff;
	--track-bg-color: #fff;
	--switch-bg-color: #191414;
	--slider-color: #edfcf2;
	--lheader-tx-color: #191414;
}
.dark {
	--main-bg-color: #191414;
	--main-tx-color: #edfcf2;
	--header-tx-color: #b7b7b7;
	--tx-color-disable: #b7b7b7;
	--lighter-bg-color: #271f1f;
	--nav-bg-color: #271f1f;
	--nav-tx-color: #edfcf2;
	/*divider*/
	--playlist-track: #fff;
	--track-hover-color: #271f1f;
	--editing-left-bg: #000;
	--track-bg-color: #000;
	--switch-bg-color: #edfcf2;
	--slider-color: #271f1f;
	--lheader-tx-color: #b7b7b7;
}
body * {
	font-family: Nunito;
}
edfcf2html,
body {
	width: 100%;
	height: 100%;
	overflow: auto;
}
a {
	color: currentColor;
	text-decoration: none;
}

/* a.active {
	color: var(--main-tx-color);
	text-decoration: none;
} */

.App {
	text-align: center;
	background-color: var(--main-bg-color);
	color: var(--main-tx-color);
	min-height: 100vh;
}

#searchBar {
	width: 520px;
	max-width: 85%;
	border-radius: 50px;
	border: none;
	outline: none;
	margin-top: 60px;
	padding: 5px 15px;
	background-color: var(--main-tx-color);
	color: var(--main-bg-color);
}

#searchBar:active {
	background-color: var(--main-tx-color);
}

body {
	background-color: var(--main-bg-color) !important;
}

::-webkit-scrollbar {
	z-index: 10;
	width: 15px;
}

::-webkit-scrollbar-track {
	box-shadow: initial 0 0 3px var(--nav-bg-color);
}

::-webkit-scrollbar-thumb {
	background: var(--nav-bg-color);
	height: 100px;
}

/*Overwrite Chrome input auotfill settings*/
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
	-webkit-box-shadow: 0 0 0 30px var(--main-tx-color) inset !important;
}

input:-webkit-autofill {
	-webkit-text-fill-color: var(--main-bg-color) !important;
}

.App-title {
	font-size: 37px;
	letter-spacing: 8px;
}

/*login*/
.login {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #191414;
}
.login-btn {
	text-align: center;
}
.login-btn-text {
	color: #edfcf2;
	font-size: 60px;
	letter-spacing: 10px;
	padding: 5vh;
}
.login-btn button {
	background-color: #54e487;
	color: #edfcf2;
	outline: none;
	border: none;
	width: 300px;
	height: 50px;
	border-radius: 30px;
	font-size: 20px;
	text-align: center;
}

/*Profile*/
.profile-wrapper {
	padding-top: 15px;
	padding-bottom: 15px;
}

.profile {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 250px;
	max-width: 100%;
	color: var(--main-tx-color);
}

.profile-info {
	text-align: left;
	max-width: 50%;
}

.profile-artist {
	text-align: right;
	padding: 20px;
}

.profile-artist-img {
	width: 150px;
	height: 150px;
	border-radius: 100px;
	object-fit: cover;
}

.profile-img {
	width: 150px;
	height: 150px;
	border-radius: 75px;
	object-fit: cover;
}

.profile-name {
	font-size: 20px;
}

.profile-name a {
	color: var(--main-tx-color);
}

.profile-name a:hover {
	color: var(--main-tx-color);
}

.profile-info-name{
	word-break: break-all;
	font-size: 40px;
	font-weight: bold;
}

.profile-info-followers {
	font-size: 18px;
}

.profile-toptracks {
	display: flex;
	flex-direction: column;
	justify-content: center;
	text-align: center;
	align-items: center;
	margin-bottom: 50px;
}
.profile-toptracks .row {
	width: 85%;
}

.profile-toptracks-title {
	text-align: left;
	color: #b7b7b7;
	font-weight: bold;
	font-size: 30px;
}

.nav {
	width: 100%;
	height: 50px;
	z-index: 1;
	top: 0;
	position: fixed;
	display: flex;
	align-items: center;
}

.nav-back {
	cursor: pointer;
	margin: 0 20px;
}

.nav-back a {
	color: var(--main-tx-color)
}

.nav-back a:hover {
	color: var(--main-tx-color)
}
.nav-switchwrapper {
	display: flex;
	justify-content: center;
	align-items: center;
}

.nav-switch {
	top: 10%;
	position: relative;
	width: 60px;
	height: 20px;
}

.nav-switch input {
	opacity: 0;
	width: 0;
	height: 0;
	border: #000 solid 1px;
}

.nav-switch-slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: var(--switch-bg-color);
	transition: 0.4s;
	border-radius: 34px;
}

.nav-switch-slider:before {
	position: absolute;
	content: '';
	height: 15px;
	width: 15px;
	top: 2px;
	left: 2px;
	background-color: var(--slider-color);
	transition: 0.4s;
	border-radius: 50px;
}

.nav-switch input:checked + .nav-switch-slider {
	background-color: var(--switch-bg-color);
}

.nav-switch input:checked + .nav-switch-slider:before {
	-webkit-transform: translateX(40px);
	transform: translateX(40px);
}

.nav-button {
	position: absolute;
	top: 10px;
	right: 80px;
	font-size: 20px;
	color: var(--main-tx-color);
	outline: none;
	width: 40px;
	height: 40px;
	border: solid 2px var(--main-tx-color);
	border-radius: 100px;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
}
.nav-button:hover {
	background-color: var(--lighter-bg-color);
}
.nav-button.signout{
	right: 30px;
}
.nav-button a,
.nav-button a:hover {
	color: var(--main-tx-color);
	text-decoration: none;
}

/*Gallery*/
.gallery {
	padding-top: 50px;
}
.track {
	display: inline-block;
	width: 220px;
	height: 200px;
	margin: 10px;
	margin-bottom: 25px;
	text-align: left;
	cursor: pointer;
	position: relative;
}

.track-img {
	width: 220px;
	height: 220px;
	border: 3px solid var(--main-tx-color);
	border-radius: 3px;
	position: absolute;
}

.track-text {
	background-color: var(--main-bg-color);
	color: var(--main-tx-color);
	opacity: 0.75;
	width: 214px;
	margin-left: 3px;
	text-align: center;
	position: absolute;
	bottom: 0;
}

.track-play {
	width: 220px;
	height: 220px;
	position: absolute;
	opacity: 0;
}

.track-play-inner {
	position: absolute;
	text-align: center;
	font-size: 27px;
	font-weight: bold;
	z-index: 1;
	border-radius: 60px;
	width: 60px;
	height: 60px;
	color: white;
	padding-top: 8px;
	margin-left: 80px;
	margin-top: 80px;
}

.bg-default {
	background-color: #1db954;
}

.bg-red {
	background-color: red;
}

.track-play:hover {
	opacity: 1;
}

.color-white {
	color: var(--main-tx-color);
}

.pt50{
	padding-top: 50px;
}

/*album*/
.album {
	margin-top: 60px;
	margin-bottom: 50px;
}
.album-cover {
	float: left;
	padding-left: 0;
}
.album-cover-img {
	width: 100%;
}

.album-info-year {
	font-size: 20px;
	text-align: left;
	color: var(--lheader-tx-color);
}

.album-info-name {
	font-size: 30px;
	text-align: left;
	font-weight: bold;
}

.album-info,
.album-cover {
	margin: 0;
	padding: 0;
}
.tracks-section {
	padding-top: 10px;
}

/*TopButton*/
.topbutton {
	position: fixed;
	bottom: 20px;
	right: 25px;
	background-color: var(--main-tx-color);
	color: var(--main-bg-color);
	font-size: 25px;
	width: 40px;
	height: 40px;
	border-radius: 100px;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	outline: none;
	border: none;
}

@media screen and (max-width: 1024px) {
  #searchBar {
    width: 85vw;
  }
  /*ArtistProfile*/
  .profile {
    min-height: 60vh;
    padding: 0;
    width: 100%;
  }

  .profile-info,
  .profile-artist {
    text-align: center;
    width: inherit;
  }

  .profile-artist {
    padding: 10px;
  }

  .profile-info {
    padding: 0px 10px 10px 10px;
  }

  .profile-info-name {
    font-size: 56px;
  }

  .profile-info-followers {
    font-size: 16px;
  }

  .profile-toptracks {
    padding: 10vh 10px 0 10px;
  }

  .profile-toptracks .row {
    width: 100%;
  }

  /*Album*/
  .album-cover-img {
    width: 300px;
    height: 300px;
  }
  .album-info-name {
    text-align: center;
  }
  .album-info-year {
    display: none;
  }

  .tracks-section {
    padding: 0 10px 0 10px;
  }

  /*Navbar*/
  .nav {
    text-align: center;
  }
}

/* Playlist */
.playlist {
	position: fixed;
	z-index: 10;
	background-color: black;
	opacity: 0.9;
	width: 100%;
	height: 100vh;
	left: 0;
	right: 0;
	bottom: 0;
	overflow-y: scroll;
	display: none;
	text-align: center;
	flex-direction: column;
	align-items: center;
}

.playlist-profile {
	width: 200px;
	height: 200px;
	border-radius: 100%;
}

.playlist-item {
	margin-bottom: 20px;
	text-align: center;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.playlist-cancel {
	cursor: pointer;
}

.playlist-title,
.playlist-cancel {
	font-size: 72px;
	color: #fff;
}

.playlist-addnew {
	margin: 25px;
	padding: 10px;
	font-size: 18px;
	letter-spacing: 1.8px;
	outline: none;
	width: 300px;
	height: 100px;
	background-color: #54e487;
	color: #fff;
	border: none;
	border-radius: 40px;
	text-transform: uppercase;
}

.playlist-item-cover {
	position: relative;
}

.playlist-item-cover--overlay {
	opacity: 0;
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	color: #fff;
	font-size: 80px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.playlist-item-cover-img {
	opacity: 1;
	width: 250px;
	height: 250px;
}

.playlist-item-cover:hover .playlist-item-cover-img {
	opacity: 0.5;
}

.playlist-item-cover:hover .playlist-item-cover--overlay {
	opacity: 1;
}

.playlist-item-name {
	font-size: 20px;
	max-height: 200px;
	color: #fff;
}
.playlist-item-name a,
.playlist-item-name a:hover {
	color: #fff;
}

/*Playlist Page*/
.playlistpage {
	padding-bottom: 30px;
	min-height: 85vh;
}
.playlist-initial {
	padding-bottom: 20px;
	/* border-bottom-style: solid;
	border-color: #262626;
	border-width: thin; */
}

.playlist-initial-cover img {
	width: 100%;
}

.playlist-initial-info {
	padding-left: 10px;
}

.playlist-initial-info .playlist-name {
	font-size: 50px;
	text-align: left;
	font-weight: bold;
	word-break: break-all;
}
.playlist-initial-info .playlist-name input {
	max-width: 100%;
}

.playlist-initial-info .playlist-label {
	font-size: 18px;
	text-align: left;
	color: var(--header-tx-color);
}
.add-section {
	margin-top: 10px;
	text-align: left;
}
.add-btn {
	color: white;
	border: none;
	outline: none;
	border-radius: 100px;
	width: 200px;
	height: 40px;
	background-color: #000;
	text-transform: capitalize;
}

/*MyPage playlist */
.playlist--me {
	left: 0px;
	right: 0px;
	bottom: 0px;
	overflow-y: scroll;
}
.playlist-addnew--me {
	margin: 25px;
	padding: 10px;
	font-size: 18px;
	letter-spacing: 1.8px;
	outline: none;
	width: 300px;
	height: 50px;
	background-color: #000;
	color: #fff;
	border: none;
	border-radius: 40px;
	text-transform: uppercase;
}

.playlist-item-initial--me {
	width: 100%;
	border-bottom-style: solid;
	border-color: #262626;
	border-width: thin;
	display: flex;
	justify-content: space-between;
	margin-bottom: 10px;
	padding: 5px;
	font-size: 20px;
	color: var(--header-tx-color);
}
.playlist-item-initial--me .displayIcons i {
	padding-left: 10px;
	padding-right: 10px;
}
.playlist--me {
	padding-top: 50px;
}
.playlist-item--me {
	margin-bottom: 20px;
}
.playlist-item-img--me {
	width: 200px;
	height: 200px;
}

/*New Playlist*/
.newPlaylist {
	position: fixed;
	z-index: 10;
	background-color: rgb(0, 0, 0, 0.5);
	width: 100%;
	height: 100vh;
	left: 0;
	right: 0;
	bottom: 0;
	display: none;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.newPlaylist-input {
	width: 100%;
	color: #fff;
	background-color: #282828;
	height: 200px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
.newPlaylist-input div {
	text-align: left;
	width: 30%;
	min-width: 350px;
}

.newPlaylist-input input {
	outline: none;
	border: none;
	height: 80px;
	font-size: 60px;
	text-align: left;
	width: 30%;
	min-width: 350px;
	color: #edfcf2;
	background-color: #282828;
}

.newPlaylist-buttons {
	width: 30%;
	min-width: 350px;
}

.newPlaylist-buttons .button--cancel,
.newPlaylist-buttons .button--create {
	width: 200px;
	height: 40px;
	border-radius: 50px;
	border: none;
	margin-left: 10px;
	margin-right: 10px;
}
.newPlaylist-exit{
	cursor: pointer;
	font-size: 30px;
}

/*Playlist Tracks*/

.playlist-tracks,
.playlist-tracks0 {
	text-align: left;
	border-bottom-style: solid;
	border-color: #262626;
	border-width: thin;
	min-height: 35px;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
.playlist-tracks-time{
	text-align: center;
}

.playlist-tracks:hover {
	background-color: var(--lighter-bg-color);
}
.playlist-tracks:hover .playlist-tracks-playButton {
	display: inline-block;
}
.playlist-tracks:hover .playlist-tracks-index {
	display: none;
}

.playlist-tracks:hover .playlist-tracks-add {
	visibility: visible;
}

.playlist-tracks-title {
	max-width: 100%;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	color: var(--playlist-track);
}
.playlist-tracks-title0,
.playlist-tracks-index0 {
	color: var(--playlist-track);
}
.tracks-artist {
	font-size: 14px;
}

.playlist-tracks-index {
	color: var(--playlist-track);
	display: inline-block;
}

.playlist-tracks-playButton {
	display: none;
}

.playlist-tracks-play,
.playlist-tracks-play--disable {
	font-size: 20px;
}

.playlist-tracks-play i {
	cursor: pointer;
}

.playlist-tracks-play--disable {
	color: var(--tx-color-disable);
}

.playlist-tracks-add {
	cursor: pointer;
	visibility: hidden;
}

.playlist-tracks-artist{
	font-size: 14px;
}
.editing {
	padding-top: 50px;
}

#left {
	height: 100%;
	width: 0vw;
	position: fixed;
	top: 0;
	left: 0;
	background-color: var(--editing-left-bg);
	color: var(--main-tx-color);
	overflow-x: hidden;
}

#left a,
#left a:hover {
	color: currentColor;
}

#right {
	margin-left: 0vw;
	transition: margin-left .5s;
}
.result-divider {
	color: var(--main-tx-color);
	font-weight: bold;
	margin-top: 25px;
	margin-bottom: 10px;
	display: flex;
	justify-content: space-between;
}
.result-divider .title {
	font-size: 20px;
}
.result-divider .showMore {
	cursor: pointer;
}
.result-divider .showMore:hover {
	text-decoration: underline;
}
.editing-row {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
	grid-gap: 15px;
}
.editing-album img {
	max-width: 100%;
}

.album-tracks,
.album-tracks0 {
	text-align: left;
	border-bottom-style: solid;
	border-color: #262626;
	border-width: thin;
	min-height: 35px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--track-bg-color);
	color: var(--main-tx-color);
}

.album-tracks0 .album-tracks-time {
	text-align: center;
}

.album-tracks:hover {
	background-color: var(--track-hover-color);
}
.album-tracks:hover .album-tracks-playButton {
	display: inline-block;
}
.album-tracks:hover .album-tracks-index {
	display: none;
}

.album-tracks:hover .album-tracks-add {
	visibility: visible;
}

.album-tracks-title {
	max-width: 100%;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	color: var(--main-tx-color);
}
.album-tracks-title0,
.album-tracks-index0 {
	color: var(--main-tx-color);
}
.tracks-artist {
	font-size: 14px;
}

.album-tracks-index {
	color: var(--main-tx-color);
	display: inline-block;
}

.album-tracks-playButton {
	display: none;
}

.album-tracks-play,
.album-tracks-play--disable {
	font-size: 20px;
}

.album-tracks-play i {
	cursor: pointer;
}

.album-tracks-play--disable {
	color: var(--tx-color-disable);
}

.album-tracks-add {
	cursor: pointer;
	visibility: hidden;
}

.album-titlerow {
	font-size: 20px;
	width: 100%;
	display: flex;
	justify-content: space-between;
	padding: 0 10px;
	font-weight: bold;
}
.album-titlerow .showAll {
	font-size: 15px;
	cursor: pointer;
}

.album-titlerow .showAll:hover {
	text-decoration: underline;
}
.album-row {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
	grid-gap: 15px;
	padding: 0 10px;
}
.artist-album img {
	max-width: 100%;
}

